import React from 'react';
import TopNavigation from 'components/topNavigation';

const BuyUnique = () => {
  return (
    <div className='view-container'>
      <div className='container'>
        <div className='row'>
          <div className='col-md-12'>
            <TopNavigation 
              location='buyUnique'
            />
          </div>
        </div>
        <div className='row'>
          <div className='col-md-12'>
            buyUnique
          </div>
        </div>
      </div>
    </div>
  )
}

export default BuyUnique


