import React from 'react';
import TopNavigation from 'components/topNavigation';

const Contacts = () => {
  return (
    <div className='view-container'>
      <div className='container'>
        <div className='row'>
          <div className='col-md-12'>
            <TopNavigation 
              location='contacts'
            />
          </div>
        </div>
        <div className='row'>
          <div className='col-md-12'>
            <div>
              contacts
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Contacts


