export const fetchPhoneById = async id => {
    return new Promise( (resolve, reject) => {
        // here is one item from database
        const phone = R.find(R.propEq('id', id), dataItems)
        resolve(phone)
        reject('error');
    })
}

export const fetchCategories = async () => {
    return new Promise( (resolve, reject) => {
        resolve(categories)
        reject('error');
    })
}


